export const ProfileContainer = compose<React.ComponentType>( //   
  connect(mapStateToProps, {setProfile: setProfileTC, setStatus: setStatusTC, updateStatus: updateStatusTC}),
  withRouter,
  //withAuthRedirect
)(ProfileComponent)